* {
  margin: 0;
  padding: 0;
}

:root {
  --pink: #e86f54;
  --white: #fff;
  --black: #000;
}

.container {
  max-width: 767px;
  margin: 0 auto;
  padding: 0 24px;
}

.list {
  list-style-type: none;
}

.section {
  padding: 100px 0;
}



.blog-section {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.blog-title {
  margin-right: 14px;
}

.line {
  width: 202px;
  height: 1px;
  background-color: #000;
}

.blog-photo-wrapper {
  flex-wrap: wrap;
  justify-content: center;
  gap: 32px;
  margin-left: 0;
  list-style: none;
  display: flex;
}

.photo-item {
  max-width: 428px;
  flex-direction: row;
  display: flex;
}

.arrow-wrapper {
  justify-content: center;
  align-items: center;
  display: flex;
}

.arrow {
  width: 50px;
  height: 50px;
  transform: rotate(-90deg);
}

.newsletter-wrapper {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.follow {
  color: #000;
  font-size: 18px;
  line-height: normal;
}

.button-newsletter {
  color: #fff;
  background-color: var(--pink, #e86f54);
  cursor: pointer;
  min-width: 170px;
  min-height: 40px;
  border: none;
  border-radius: 38px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin: 0 auto;
  padding: 14px 28px;
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.footer-container {
  width: 100%;
  max-width: 768px;
  background: #000;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.footer-nav-wrapper {
  max-width: 480px;
  flex-wrap: wrap;
  align-items: baseline;
  display: flex;
}

.footer-nav {
  flex-direction: column;
  justify-content: left;
  margin-top: 32px;
  margin-bottom: 32px;
  padding: 0;
  display: flex;
}

.footer-nav-title {
  color: #fff;
  margin-bottom: 18px;
  font-size: 18px;
  font-weight: bold;
  line-height: normal;
  list-style: none;
}

.footer-nav-info {
  letter-spacing: .02em;
  color: #fff;
  margin-bottom: 12px;
  font-size: 14px;
  font-weight: 500;
  line-height: normal;
  list-style: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.link-active {
  color: #fff;
  text-decoration: none;
}

.footer-social-position {
  flex-direction: row;
  align-items: baseline;
  display: flex;
}

.footer-social-link {
  width: 100%;
  height: 100%;
  background-color: #0000;
  justify-content: center;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.footer-social-icon-color {
  color: #fff;
}

@media screen and (min-width: 480px) {
  .photo-item {
    width: 136px;
    height: 181px;
  }
}

@media screen and (min-width: 768px) {
  .photo-item {
    width: 344px;
    height: 398px;
  }
}





